body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.form-group {
    margin-bottom: 20px;
  }
  
  label {
    margin-bottom: 10px;
  }
  
  input {
    border: 0 !important;
    box-shadow: none !important;
    border-bottom: 2px solid #2c3e50 !important;
    transition: all 0.3s ease-in-out;
  }
  
  #msg {
    color: #c0392b;
    text-transform: capitalize;
    margin-top: 5px;
  }
  
  #msg span:first-child {
    margin-right: 10px;
  }
.full-width {
    width: 100%;
    transition: all 0.3s ease-in-out;
  }
  
  .full-width:hover {
    background: linear-gradient(to left, #2676ac, #2a81ba);
  }
  
  .modal-header {
    background: linear-gradient(to left, #2557b5, #cbe8fa);
  }
  .modal-body {
    background-size: cover;
    padding: 50px;
  
    padding-bottom: 50px;
  }
.page-link:hover {
    background-color: #0d6dfdc4;
    color: white;
}

.page-item.active {
    background-color: #0d6efd !important;
}
.table-row {
    cursor: pointer !important;
    transition: all 0.3s ease-in;
  }
  
  .table-row:hover {
    background-color: #1d79a6;
    color: white;
    -webkit-transform: translateX(4px);
            transform: translateX(4px);
    overflow: hidden;
  }
  
  .role {
    text-transform: capitalize;
  }
  
  #actions button {
    background-color: transparent;
    border: 0;
  }
  
  .selected {
    background-color: #1d79a6;
    color: white;
  }
  
  #actions button:first-child {
    margin-right: 20px;
  }
  
  #actions button:last-child {
    color: #c0392b !important;
  }
  
  tr:hover td span i,
  .selected td span i {
    color: white !important;
  }
  
  .align-left {
    float: left;
  }
  
  #deleteAll span:last-child {
    margin-left: 10px;
  }
  
  #deleteAll {
    transition: all 0.3s ease-in-out;
  }
  
  #deleteAll.active {
    border: 1px solid #c0392b;
    color: #c0392b;
  }
  
  #deleteAll.active:hover {
    background-color: #c0392b;
    color: white;
  }
  
  #deleteAll.disabled {
    border: 1px solid #2c3e50;
    color: #2c3e50;
  }
  .table {
    background-color: white;
    opacity: 0.85;
    border-radius: 10px;
  }
  
  @media screen and (max-width: 768px) {
    .table {
      width: auto;
      padding: 0px;
      margin: 0px;
    }
  }
#no_data h5 {
    color: #f39c12;
    font-size: 35px;
  }
  
  #no_data h5 span {
    margin-left: 5px;
  }
  .body {
    background-size: cover;
    padding: 50px;
    border-radius: 20px;
    padding-bottom: 50px;
  }
  .theme {
    background: linear-gradient(to left, #2557b5, #cbe8fa);
    border-radius: 10px;
  }
  .heading {
    width: 100%;
    display: flex;
    font-size: xx-large;
    justify-content: center;
    color: black;
    font-weight: 900px !important;
  }
  img {
    width: 45px;
  }
  @media screen and (max-width: 768px) {
    .body {
      width: 100vw;
      background-size: cover;
      padding: 10px;
      align-items: center;
    }
  }
input {
    border: 1px solid #2c3e50 !important;
  }
