.form-group {
    margin-bottom: 20px;
  }
  
  label {
    margin-bottom: 10px;
  }
  
  input {
    border: 0 !important;
    box-shadow: none !important;
    border-bottom: 2px solid #2c3e50 !important;
    transition: all 0.3s ease-in-out;
  }
  
  #msg {
    color: #c0392b;
    text-transform: capitalize;
    margin-top: 5px;
  }
  
  #msg span:first-child {
    margin-right: 10px;
  }